ve;\n  vertical-align: baseline;\n}\n\nsub {\n  bottom: -0.25em;\n}\n\nsup {\n  top: -0.5em;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Remove the border on images inside links in IE 10.\n */\n\nimg {\n  border-style: none;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * 1. Change the font styles in all browsers.\n * 2. Remove the margin in Firefox and Safari.\n */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n  font-family: inherit; /* 1 */\n  font-size: 100%; /* 1 */\n  line-height: 1.15; /* 1 */\n  margin: 0; /* 2 */\n}\n\n/**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n\nbutton,\ninput { /* 1 */\n  overflow: visible;\n}\n\n/**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n\nbutton,\nselect { /* 1 */\n  text-transform: none;\n}\n\n/**\n * Correct the inability to style clickable types in iOS and Safari.\n */\n\nbutton,\n[type=\"button\"],\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button;\n}\n\n/**\n * Remove the inner border and padding in Firefox.\n */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  border-style: none;\n  padding: 0;\n}\n\n/**\n * Restore the focus styles unset by the previous rule.\n */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n  outline: 1px dotted ButtonText;\n}\n\n/**\n * Correct the padding in Firefox.\n */\n\nfieldset {\n  padding: 0.35em 0.75em 0.625em;\n}\n\n/**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n */\n\nlegend {\n  box-sizing: border-box; /* 1 */\n  color: inherit; /* 2 */\n  display: table; /* 1 */\n  max-width: 100%; /* 1 */\n  padding: 0; /* 3 */\n  white-space: normal; /* 1 */\n}\n\n/**\n * Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n  vertical-align: baseline;\n}\n\n/**\n * Remove the default vertical scrollbar in IE 10+.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10.\n * 2. Remove the padding in IE 10.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n  box-sizing: border-box; /* 1 */\n  padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n  -webkit-appearance: textfield; /* 1 */\n  outline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n  -webkit-appearance: button; /* 1 */\n  font: inherit; /* 2 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in Edge, IE 10+, and Firefox.\n */\n\ndetails {\n  display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n  display: list-item;\n}\n\n/* Misc\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10+.\n */\n\ntemplate {\n  display: none;\n}\n\n/**\n * Add the correct display in IE 10.\n */\n\n[hidden] {\n  display: none;\n}\n","@font-face {\n    font-family: '72-Regular';\n    src: url('/ui5con/onair2020/fonts/72-Regular.woff2') format('woff2'),\n         url('/ui5con/onair2020/fonts/72-Regular.woff') format('woff');\n    }\n\n@font-face {\n    font-family: '72-Light';\n    src: url('/ui5con/onair2020/fonts/72-Light.woff2') format('woff2'),\n         url('/ui5con/onair2020/fonts/72-Light.woff') format('woff');\n    }\n","html {\n     font: 16px/24px \"72-Regular\", Arial, Sans-Serif;\n }\n\n*, *:before, *:after {\n    box-sizing: border-box;\n}\n\n",".section {\n\n    &.mod_sponsor {\n        /* background: #fb5b38 */\n        background: rgb(251,91,56);\n        background: linear-gradient(343deg, rgba(251,91,56,1) 54%, rgba(253,187,45,1) 100%);\n    }\n\n    &.mod_notes {\n        /* background: #fb5b38 */\n        background: rgb(251,91,56);\n        background: linear-gradient(343deg, rgba(251,91,56,1) 54%, rgba(253,187,45,1) 100%);\n\n        & .section__content {\n            padding: 80px 0 30px;\n            display: flex;\n            justify-content: center;\n            flex-wrap: wrap;\n            align-items: stretch;\n            align-content: center;\n\n            & > div {\n                flex-basis: 33%;\n\n                &:last-child {\n                    @media (max-width: 767px) {\n                        flex-basis: 100%;\n                    }\n                }\n\n                @media (max-width: 1000px) {\n                    flex-basis: 50%;\n                }\n            }\n\n\n            @media (max-width: 767px) {\n                padding: 50px 20px 25px;\n            }\n\n        }\n\n    }\n\n    &.mod_highlights {\n        background: #141212\n    }\n\n    &.mod_copyright {\n        & .section__content {\n            padding-top: 100px;\n            padding-bottom: 100px;\n\n            @media (max-width: 1023px) {\n                padding-top: 40px;\n                padding-bottom: 40px;\n            }\n\n            @media (max-width: 767px) {\n                padding-top: 40px;\n                padding-bottom: 20px;\n            }\n        }\n    }\n\n    & .section__content {\n        max-width: 1240px;\n        margin: 0 auto;\n        padding: 160px 50px;\n\n        @media (max-width: 1023px) {\n            padding: 80px 30px;\n        }\n\n        @media (max-width: 767px) {\n            padding: 60px 20px;\n        }\n    }\n}\n\n\n\n",".iconText {\n    display: flex;\n    align-items: center;\n    font-family: \"72-Light\", Arial, Sans-Serif;\n    font-size: 30px;\n    line-height: 1.8em;\n    opacity: 0.8;\n\n    @media (max-width: 767px) {\n        font-size: 18px;\n    }\n\n    &.color_light {\n        color: #ffa42c;\n    }\n\n    &.color_white {\n        color: #ffffff;\n\n        & a {\n            color: #ffffff;\n        }\n    }\n\n    &.mod_people nobr {\n        @media (max-width: 767px) {\n            white-space: normal;\n        }\n    }\n\n    &.type_vertical {\n        flex-direction: column;\n        margin-bottom: 50px;\n\n        @media (max-width: 767px) {\n            margin-bottom: 25px;\n        }\n\n        & .iconText__text {\n            text-align: center;\n            max-width: 250px;\n            font-size: 22px;\n            line-height: 1.3;\n\n            @media (max-width: 767px) {\n                font-size: 18px;\n            }\n\n            & a {\n                text-decoration: none;\n                border-bottom: 2px rgba(255, 255, 255, 0.4) solid;\n\n                &:hover {\n                    border-bottom: 2px rgba(255, 255, 255, 0.7) solid;\n                }\n\n                &:active {\n                    border-bottom: 2px rgba(255, 255, 255, 0.8) solid;\n                }\n            }\n        }\n\n        & .iconText__icon {\n            margin: 0 0 20px 0;\n            display: block;\n            text-decoration: none;\n            width: 96px;\n            height: 96px;\n            background: rgba(20,18,18,.2) 50% 50% no-repeat;\n            background-size: 48px 48px;\n            border-radius: 50%;\n\n            @media (max-width: 767px) {\n                width: 64px;\n                height: 64px;\n                background-size: 32px 32px;\n            }\n        }\n    }\n\n    & .iconText__icon {\n        width: 24px;\n        height: 24px;\n        margin-right: 20px;\n        display: inline-block;\n\n        @media (max-width: 767px) {\n            width: 18px;\n            height: 18px;\n            margin-right: 12px;\n        }\n\n        &.type_pin {\n            background: url('./pin.svg') 50% 50% no-repeat;\n        }\n\n        &.type_calendar {\n            background: url('./calendar.svg') 50% 50% no-repeat;\n        }\n\n        &.type_location {\n            background-image: url('/ui5con/onair2020/location.svg');\n        }\n\n        &.type_people {\n            background-image: url('/ui5con/onair2020/people.svg');\n        }\n\n        &.type_play {\n            background-image: url('/ui5con/onair2020/play.svg');\n            background-position: 58% 50%;\n        }\n\n        &.type_twitter {\n            background-image: url('/ui5con/onair2020/twitter.svg');\n        }\n\n        &.type_email {\n            background-image: url('/ui5con/onair2020/email.svg');\n        }\n    }\n\n    & .iconText__text {\n        display: inline-block;\n    }\n\n\n}"]}]);
